import React, { useState, useContext } from 'react';
import { HealthContext } from '../../context/healthContext.js';

const Recipe = props => {
  const { recipeStore } = useContext(HealthContext);
  const [recipe, setRecipe] = recipeStore;
  console.log('recipe', recipe);

  return (
    <div className="post">
      <div className="post-one">
        <img className="post-image" src={recipe.imageURL}
        onClick={(e) => {this.props.handleClick();}}
        />
        <h2><b>{recipe.name}</b></h2>
        <p>like: {recipe.like}</p>
        <br></br>
        <img className="post-user" src={recipe.userURL}
        onClick={(e) => {this.props.handleClick();}}
        />
        <p>{recipe.user}</p>

        <br></br>
        <h3><b>Method</b></h3>
        {recipe.methods.map((item, i) => {
          if (item) {
            return (
              <div key={i}>
                <h4><b>Step: {i}</b></h4>
                <img src={item.imageURL} />
                <p>{item.description}</p>
                <br></br>
              </div>
            )
          }
        })}
      </div>

      <div className="post-two">
        <br></br>
         <h3><b>Ingredients</b></h3>
          {recipe.ingredients.map((item, i) => {
            return <p key={i}>{item}</p>
          })}
      </div>

    </div>
  );
}

export default Recipe;
